import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Experience from "./views/Resume/Experience.vue";
import TechSkills from "./views/Resume/TechSkills.vue";
import OtherInfo from "./views/Resume/OtherInfo.vue";
const firebase = require("firebase/app");

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: "/",
            name: "home",
            component: Home
        },
        {
            path: "/about",
            name: "about",
            component: () => import("./views/About.vue"),
            meta: { requiresAuth: true }
        },
        {
            path: "/work",
            name: "work",
            component: () => import("./views/Work.vue")
        },
        {
            path: "/login",
            name: "login",
            component: () => import("./views/Login.vue")
        },
        {
            path: "/resume/experience",
            name: "experience",
            component: Experience
        },
        {
            path: "/resume/technical-skills",
            name: "techskills",
            component: TechSkills
        },
        {
            path: "/resume/other-info",
            name: "otherinfo",
            component: OtherInfo
        }
    ]
});

router.beforeEach((to, from, next) => {
    if (to.name !== "login")
        localStorage.setItem("redirectAfterLogin", to.name);
    const isProtectedRoute = to.matched.some(
        record => record.meta.requiresAuth
    );
    const user = firebase.auth().currentUser;
    if (isProtectedRoute && user === null) {
        next({ name: "login" });
    } else {
        next();
    }
});

export default router;
