<template>
  <div class="typewriter">
    <p class="output" ref="output"></p>
  </div>
</template>
<script>
export default {
  name: "Typewriter",
  props: ["typeSpeed", "eraseSpeed", "phrases", "numOfLoops", "delay"],
  data() {
    return {
      output: "",
      currentMessageIndex: 0,
      count: 0,
      isInfinite: this.numOfLoops === undefined,
      writeInterval: undefined,
      eraseInterval: undefined
    };
  },
  mounted: function() {
    const _this = this;
    const delay = this.delay ? this.delay : 0;

    setTimeout(function() {
      _this.typeWrite();
    }, delay);
  },
  methods: {
    typeWrite: function() {
      const speed = this.typeSpeed ? this.typeSpeed : 70;
      if (this.currentMessageIndex == this.phrases.length - 1) {
        this.count++;
      }
      if (!this.phrases[this.currentMessageIndex]) {
        this.currentMessageIndex = 0;
      }
      const currentStr = this.phrases[this.currentMessageIndex];
      currentStr.split();
      let part = "";
      let currentLetter = 0;
      this.writeInterval = setInterval(() => {
        if (!currentStr[currentLetter]) {
          this.currentMessageIndex++;
          if (this.count < this.numOfLoops || this.isInfinite) {
            setTimeout(() => {
              this.deleteMessage(part);
            }, 500);
            clearInterval(this.writeInterval);
          } else {
            clearInterval(this.writeInterval);
          }
        } else {
          part += currentStr[currentLetter++];
          if (this.$refs.output !== undefined) {
            this.$refs.output.innerHTML = part;
          } else clearInterval(this.eraseInterval);
        }
      }, speed);
    },
    deleteMessage: function(str) {
      const speed = this.eraseSpeed ? this.eraseSpeed : 50;

      this.eraseInterval = setInterval(() => {
        //console.log("borrando:", this);
        if (str.length === 0) {
          this.typeWrite(); //SetTimeout to wait a little before next phrase
          clearInterval(this.eraseInterval);
        } else {
          str = str.split("");
          str.pop();
          str = str.join("");
          if (this.$refs.output !== undefined) {
            this.$refs.output.innerHTML = str;
            //console.log("clear erase", this.writeInterval);
          } else clearInterval(this.eraseInterval);
          //console.log(this.$refs.output);
        }
      }, speed);
    },
    killAll: function() {}
  },
  beforeDestroy: function() {
    clearInterval(this.writeInterval);
    clearInterval(this.eraseInterval);
  }
};
</script>

<style lang="scss">
.typewriter {
  min-height: 17px;
  .output {
    display: inline-block;
    &:after {
      content: "_";
      font-weight: bold;
      position: relative;
      transition: all 100ms;
      animation: cursor 1s infinite;
      left: 1px;
    }
  }
  @keyframes cursor {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
    100% {
    }
  }
}
</style>