<template>
  <div class="progress">
    <div class="label">{{label}}</div>
    <div class="container">
      <div v-if="!hideInfo" class="description" :style="`left:${percentValue}%`">
        <span v-if="completed>max">></span>
        {{(completed>max) ? max : completed}}
        <span v-if="measure">{{measure}}</span>
      </div>
      <div
        class="percent"
        :style="`width:${styleValue}%;transition-timing-function: cubic-bezier(${cubic1},${cubic2}, ${cubic3}, ${cubic4});`"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Progress",
  props: ["max", "completed", "label", "measure", "hideInfo"],
  data() {
    return {
      styleValue: 0,
      cubic1: Math.random(),
      cubic2: Math.random(),
      cubic3: Math.random(),
      cubic4: Math.random()
    };
  },
  mounted: function() {
    setTimeout(() => (this.styleValue = this.percentValue), 1000);
  },
  computed: {
    percentValue: function() {
      let i = 0;
      if (this.completed > this.max) i = this.max;
      else i = this.completed;
      return (100 * i) / this.max;
    }
  }
};
</script>

<style lang="scss">
.progress {
  margin-bottom: 1.5rem;
  .container {
    width: 100%;
    background-color: #ccc;
    position: relative;
    height: 3px;
    display: flex;
  }
  .label {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  .percent {
    position: absolute;
    background-color: $main-color;
    top: 0;
    left: 0;
    height: 3px;
    //width: 0% !important;
    transition: all 400ms 1s;

    &.loaded {
      width: initial;
    }
  }
  .description {
    position: absolute;
    top: -15px;
    font-size: 0.8rem;
    left: 75%;
    transform: translateX(-50%);
    animation: appear;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-delay: 3s;
    visibility: hidden;
    width: 80px;
  }
  @keyframes appear {
    to {
      visibility: visible;
    }
  }
}
</style>