import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "vue-material-design-icons/styles.css";
// Firebase App (the core Firebase SDK) is always required and must be listed first
import * as firebase from "firebase/app";

import "firebase/auth";
import "firebase/firestore";

import VueAnalytics from 'vue-analytics'

const firebaseConfig = {
  apiKey: "AIzaSyAgrZ15AhTehW8YRPsZrItbRvDIiJSUTQA",
  authDomain: "cv-github.firebaseapp.com",
  databaseURL: "https://cv-github.firebaseio.com",
  projectId: "cv-github",
  storageBucket: "",
  messagingSenderId: "892348300741",
  appId: "1:892348300741:web:687f6fc188d0ce43461fac"
};

// Initialize Firebase
const firebaseApp = firebase.initializeApp(firebaseConfig)
//firebase.firestore().settings({timestampsInSnapshots: true})
export default firebaseApp.firestore();
//firebase.initializeApp(firebaseConfig);

Vue.config.productionTip = false;

firebase.auth().onAuthStateChanged(user => {
  //console.log(user);
  if (user) {
    store.dispatch("detectUser", { email: user.email, uid: user.uid });
  } else {
    store.dispatch("detectUser", null);
  }
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
});


Vue.use(VueAnalytics, {
  id: 'UA-151020257-1',
  router
})