<template>
  <header class="site-header">
    <div class="logo">
      <router-link to="/">
        <span>AS</span>
        <img src="../assets/spades.svg" alt="logo" />
      </router-link>
    </div>
    <div class="menu-wrapper">
      <ul id="nav" :class="{ show: showMobileNav }">
        <li>
          <router-link to="/">Home</router-link>
        </li>

        <li>
          <router-link to="/work">Work</router-link>
        </li>
        <li class="has-dropdown">
          <div class="item">
            Resume
            <ChevronDownIcon />
          </div>
          <ul class="dropdown-menu">
            <li>
              <router-link to="/resume/experience">Experience</router-link>
            </li>
            <li>
              <router-link to="/resume/technical-skills">Tech Skills</router-link>
            </li>
            <li>
              <router-link to="/resume/other-info">Others</router-link>
            </li>
          </ul>
        </li>
        <li>
          <router-link to="/about">About</router-link>
        </li>
      </ul>
      <div class="logout-wrapper" v-if="userExist">
        <a @click="logout">
          <LogoutVariantIcon />
        </a>
      </div>
    </div>
    <button class="menu-btn">
      <MenuIcon :class="['menu',{'visible':!showMobileNav}]" @click="showMenu()" />
      <CloseIcon :class="['close',{'visible':showMobileNav}]" @click="hideMenu()" />
    </button>
  </header>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Typewriter from "@/components/TypeWriter.vue";
import ChevronDownIcon from "vue-material-design-icons/ChevronDown.vue";
import MenuIcon from "vue-material-design-icons/Menu.vue";
import CloseIcon from "vue-material-design-icons/Close.vue";
import LogoutVariantIcon from "vue-material-design-icons/LogoutVariant.vue";

export default {
  components: {
    ChevronDownIcon,
    MenuIcon,
    Typewriter,
    CloseIcon,
    LogoutVariantIcon
  },
  data: function() {
    return {
      showMobileNav: false
    };
  },
  methods: {
    ...mapActions(["logout"]),

    showMenu: function() {
      this.showMobileNav = true;
    },
    hideMenu: function() {
      this.showMobileNav = false;
    }
  },
  computed: {
    ...mapGetters(["userExist"])
  },
  watch: {
    $route(to, from) {
      this.showMobileNav = false;
    }
  }
};
</script>

<style lang="scss">
.site-header {
  //border: 1px solid red;
  position: fixed;
  width: 100%;
  left: 0;
  top: $app-space;
  background-color: rgba(255, 255, 255, 1);
  padding: 1rem $app-space * 2;
  height: 6rem;
  z-index: 9;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 767px) {
    height: 4rem;
  }
  .logo {
    a {
      display: block;
    }
  }
  .menu-wrapper {
    display: flex;
    align-items: center;
    .logout-wrapper {
      margin-left: 1.7rem;
      a {
        &:hover {
          cursor: pointer;
        }
      }
      svg {
        color: $main-color;
        position: static;
        width: 1.1rem;
        height: 1.1rem;
      }
    }
  }
  .menu-btn {
    display: none;
    background: transparent;
    border: 0;
    @media screen and (max-width: 767px) {
      padding-right: 0;
    }
    &:hover {
      cursor: pointer;
    }
    .menu,
    .close {
      display: none;
      &.visible {
        display: block;
      }
    }

    .material-design-icon__svg {
      width: 2rem !important;
      height: auto !important;
      position: relative;
      color: $main-color;
    }
    @media screen and (max-width: 767px) {
      display: block;
    }
  }
  .logo {
    width: 40px;
    position: relative;

    img {
      width: 100%;
    }
    span {
      color: #fff;
      position: absolute;
      text-align: center;
      top: 50%;
      width: 100%;
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
    }
  }
  #nav {
    margin-left: 1.7rem;
    @media screen and (max-width: 767px) {
      //display: none;
      padding: 3rem 0 2rem;
      width: 100%;
      box-shadow: -2px 2px 7px rgba(0, 0, 0, 0.2);
      position: absolute;
      right: -100%;
      height: 100vh;
      top: 96px;
      transition: $g-trans;
      background-color: white;
      z-index: 1;
      &.show {
        right: 0;
      }
    }
    li {
      color: $font-color;
      text-decoration: none;
      display: inline-flex;
      padding: 0 1.7rem;
      font-size: 0.95rem;
      transition: $g-trans;
      position: relative;
      @media screen and (max-width: 767px) {
        display: block;
        display: block;
        text-align: center;
        margin-bottom: 1.5rem;
        font-size: 1.3rem;
      }
      a,
      span {
        text-decoration: none;
        &.router-link-exact-active {
          color: $main-color;
          border-bottom: 1px solid $main-color;
          padding-bottom: 0.2rem;
          &:hover {
            color: $main-color;
          }
        }
        &.chevron-down-icon {
          position: relative;
          top: 1px;
        }
      }
      .dropdown-menu {
        visibility: hidden;
        opacity: 0;
        transition: $g-trans;
        position: absolute;
        left: 0;
        width: 150%;
        background-color: white;
        top: 35px;
        padding-top: 1rem;
        box-shadow: -2px 2px 7px rgba(0, 0, 0, 0.2);
        @media screen and (max-width: 767px) {
          position: static;
          opacity: 1;
          visibility: visible;
          box-shadow: none;
          width: auto;
          padding-top: 2rem;
        }
        li {
          display: block;
          margin-bottom: 1rem;
          font-size: 0.9rem;
          border-bottom: 1px solid #eee;
          padding-bottom: 0.9rem;

          @media screen and (max-width: 767px) {
            font-size: 0.9rem;
            border-bottom: 0;
          }
          &:last-child {
            margin-bottom: 0;
          }
          .router-link-exact-active {
            border: 0;
            text-decoration: none;
          }
        }
      }
      &:hover {
        .dropdown-menu {
          visibility: visible;
          opacity: 1;
        }
      }
      &.has-dropdown {
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}
</style>