<template>
  <div class="experience-page">
    <div class="breadcrumb">
      <typewriter typeSpeed="50" :phrases="breadcrumb" numOfLoops="1"></typewriter>
    </div>
    <header class="page-header">
      <h1 class="title">WORK EXPERIENCE</h1>
      <div class="desc">This is my work experience timeline</div>
    </header>
    <div class="page-container">
      <ul class="timeline">
        <li v-for="(el, index) in experience" :key="index">
          <div :class="(index % 2 === 0) ? 'direction-l' : 'direction-r'">
            <div class="flag-wrapper">
              <div class="time-wrapper">
                <span class="time">{{getPrettyDate(el.startDate)}}  {{getPrettyDate(el.endDate)}}</span>
              </div>
              <div
                :class="['flag', el.company.toLowerCase().replace(/ /g, '').normalize('NFD').replace(/[\u0300-\u036f]/g, '')]"
              >
                <span class="city">[{{el.city}}]</span>
                {{el.company}}
              </div>
              <div class="position">{{el.position}}</div>
            </div>
            <div class="description">
              <div class="title">Description</div>
              <div class="content-info">
                <span v-for="(item,index) in el.description" :key="index">
                  {{item}}
                  <br />
                </span>
              </div>
            </div>
            <div class="tech-stack">
              <div class="title">Tech Stack</div>
              <div class="content-info">
                <span v-for="(item,index) in el.stack" :key="index">{{item}}</span>
              </div>
            </div>
            <div v-if="el.projects !== undefined" class="projects">
              <div class="title">Relevant Projects</div>
              <div class="content-info">
                <a v-for="(item,index) in el.projects" :key="index" :href="item.url">
                  {{item.name}}
                  <br />
                </a>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Typewriter from "@/components/TypeWriter.vue";

import CheckUnderlineIcon from "vue-material-design-icons/ChevronDown.vue";
import db from "@/main";

export default {
  name: "experiencie",
  components: { CheckUnderlineIcon, Typewriter },
  data: function() {
    return {
      experience: [],
      breadcrumb: [":~ Init > resume > experience"]
    };
  },
  created() {
    this.fetchData();
  },
  computed: {
    ...mapState(["user"])
  },
  methods: {
    fetchData() {
      db.collection("experience")
        .orderBy("startDate", "desc")
        .get()
        .then(snapshot => {
          snapshot.forEach(w => {
            this.experience.push(w.data());
          });
        });
    },
    getPrettyDate(date) {
      if (date === null) return "Present";
      const realDate = date.toDate();
      return `${realDate.toLocaleString("EN", {
        month: "short"
      })} ${date.toDate().getFullYear()}`;
    }
  }
};
</script>

<style lang="scss" scoped>
.experience-page {
  padding-top: 6rem;
  position: relative;
  @media screen and (max-width: 1180px) {
    padding-left: 95px;
    padding-right: 95px;
  }
  @media screen and (max-width: 767px) {
    padding-left: 0;
    padding-right: 0;
    padding-top: 1rem;
  }
  .timeline {
    position: relative;
    width: 100%;
    margin: 0 auto;
    margin-top: 20px;
    padding: 1em 0;
    list-style-type: none;
    &:before {
      position: absolute;
      left: 50%;
      top: 0;
      content: " ";
      display: block;
      width: 1px;
      height: 100%;
      margin-left: -3px;
      background-color: #ccc;
      z-index: 5;
      @media screen and (max-width: 1180px) {
        left: 0;
      }
    }
    li {
      padding: 1em 0 3em 0;
      @media screen and (max-width: 1180px) {
        margin-left: 20px !important;
      }
      &:after {
        content: "";
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
      }
    }
  }

  .direction-l {
    position: relative;
    width: 465px;
    float: left;
    text-align: right;
    @media screen and (max-width: 1180px) {
      text-align: left;
      width: auto;
      float: none;
      margin-left: 20px;
    }
    @media screen and (max-width: 767px) {
      margin-left: 0;
    }
  }

  .direction-r {
    position: relative;
    width: 465px;
    float: right;
    @media screen and (max-width: 1180px) {
      text-align: left;
      width: auto;
      float: none;
      margin-left: 20px;
    }
    @media screen and (max-width: 767px) {
      margin-left: 0;
    }
  }

  .flag-wrapper {
    position: relative;
    display: inline-block;
    text-align: center;
    width: 300px;
    @media screen and (max-width: 1180px) {
      width: auto;
      text-align: left;
    }
  }

  .flag {
    position: relative;
    padding: 0;
    border-radius: 5px;
    font-weight: 600;
    text-align: right;
    font-size: 1.2rem;
    display: flex;
    justify-content: flex-end;
    @media screen and (max-width: 1180px) {
      text-align: left;
      display: block;
    }
    .city {
      font-weight: normal;
      font-size: 0.8rem;
      letter-spacing: -1px;
      padding: 0 10px;
      @media screen and (max-width: 1180px) {
        padding-left: 0;
      }
      @media screen and (max-width: 767px) {
        display: block;
        margin-bottom: 15px;
      }
    }
  }

  .position {
    font-size: 0.8rem;
    margin-top: 0.3rem;
  }

  .direction-l .flag:before {
    position: absolute;
    top: -100%;
    right: -76px;
    content: " ";
    display: block;
    width: 60px;
    height: 60px;

    background-size: 80%;
    background-repeat: no-repeat;
    background-position: center;
    background-color: white;
    border-radius: 60px;
    border: 2px solid #eee;
    z-index: 8;
    @media screen and (max-width: 1180px) {
      right: auto;
      left: -75px;
    }
    @media screen and (max-width: 767px) {
      width: 30px;
      height: 30px;
      left: -39px;
    }
  }

  .direction-r .flag:before {
    position: absolute;
    top: -100%;

    content: " ";
    display: block;
    width: 60px;
    height: 60px;
    background-size: 80%;
    background-repeat: no-repeat;
    background-position: center;
    background-color: white;
    border-radius: 60px;
    border: 2px solid #eee;
    z-index: 10;
    left: -82px;
    @media screen and (max-width: 1180px) {
      left: 0;
      right: auto;
      left: -75px;
    }
    @media screen and (max-width: 767px) {
      width: 30px;
      height: 30px;
      left: -39px;
    }
  }
  .direction-l .flag,
  .direction-r .flag {
    &.zentensesl {
      &:before {
        background-image: url("../../assets/zentense.jpg") !important;
      }
    }
    &.escolaespai {
      &:before {
        background-image: url("../../assets/espai.jpg") !important;
      }
    }
    &.qsystems {
      &:before {
        background-image: url("../../assets/qsystems.jpg") !important;
      }
    }
    &.webtrade {
      &:before {
        background-image: url("../../assets/webtrade.jpg") !important;
      }
    }
    &.freelance {
      &:before {
        background-image: url("../../assets/freelance.jpg") !important;
      }
    }
    &.elperiodicodecatalunya {
      &:before {
        background-image: url("../../assets/elperiodicodecatalunya.jpg") !important;
      }
    }
    &.haiku-media {
      &:before {
        background-image: url("../../assets/haiku-media.jpg") !important;
      }
    }
  }
  .time-wrapper {
    display: block;
    line-height: 1rem;
    font-size: 0.76666rem;
    color: $main-color;
  }

  .time {
    display: inline-block;
    padding-bottom: 0.5rem;
  }

  .description,
  .tech-stack,
  .projects {
    margin: 1rem 0 0 0;
    font-size: 0.77777rem;
    line-height: 1.5em;
    .title {
      font-weight: 600;
      font-size: 0.9rem;
      margin-bottom: 0.5rem;
    }
  }

  .tech-stack {
    .content-info {
      display: flex;
      flex-wrap: wrap;
      align-items: unset;
      justify-content: flex-end;
      @media screen and (max-width: 1180px) {
        justify-content: flex-start !important;
      }

      span {
        background-color: #eee;
        padding: 0.3rem;
        margin: 0.5rem;
        box-shadow: -1px 1px 5px rgba(0, 0, 0, 0.2);
      }
    }
  }

  .direction-l {
    .time-wrapper,
    .position {
      float: none;
      text-align: right;
      @media screen and (max-width: 1180px) {
        text-align: left;
        float: none;
      }
    }
    .tech-stack {
      span {
      }
    }
  }

  .direction-r {
    .time-wrapper,
    .position {
      float: none;
      text-align: left;
      @media screen and (max-width: 1180px) {
        text-align: left;
      }
    }
    .flag {
      text-align: left;
      justify-content: flex-start;
      .city {
        order: 2;
      }
    }
    .tech-stack {
      .content-info {
        justify-content: flex-start;
        @media screen and (max-width: 1180px) {
          justify-content: flex-end;
        }
      }
    }
  }
}
</style>
