import Vue from "vue";
import Vuex from "vuex";
import router from "./router";

const firebase = require("firebase/app");

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        user: "",
        error: "",
        redierctAfterLogin: "/"
    },
    mutations: {
        setUser(state, payload) {
            state.user = payload;
        },
        setError(state, payload) {
            state.error = payload;
        },
        setRedierctAfterLogin(state, payload) {
            state.redierctAfterLogin = payload;
        }
    },
    actions: {
        loginUser({ commit }, payload) {
            firebase
                .auth()
                .signInWithEmailAndPassword(payload.email, payload.password)
                .then(resp => {
                    commit("setUser", {
                        email: resp.user.email,
                        uid: resp.user.uid
                    });
                    const pageName = localStorage.getItem("redirectAfterLogin");
                    router.push({ name: pageName });
                })
                .catch(error => {
                    commit("setError", error.message);
                });
        },
        detectUser({ commit }, payload) {
            if (payload !== null)
                commit("setUser", { email: payload.email, uid: payload.uid });
            else commit("setUser", null);
        },
        logout({ commit }) {
            firebase.auth().signOut();
            commit("setUser", null);
            router.push({ name: "login" });
        }
    },
    getters: {
        userExist(state) {
            if (
                state.user === null ||
                state.user === "" ||
                state.user === undefined
            ) {
                return false;
            } else return true;
        }
    }
});
